.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
}

.card {
    width: 300px;
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 20px;
    margin: 20px;
}

button {
    margin-top: 10px;
    cursor: pointer;
}




















footer {
    padding: 100px 0 25px 0;
    display: block;

    @media (max-width:576px) {
        display: none;
    }

    .footer-container {
        max-width: 1180px;
        margin: 0 auto;
        padding: 0 20px;

        .footer-containt {
            display: flex;
            justify-content: space-between;
            flex-wrap: nowrap;
            gap: 20px;
            padding: 0 0 40px 0;
            border-bottom: 1px solid #d9d9d9;

            @media (max-width:576px) {
                flex-wrap: wrap;
            }

            @media (max-width:800px) {
                flex-wrap: wrap;
            }

            .footer-contant-pera {

                img {


                    margin: 0 0 15px 0;
                }

                p {
                    font-size: 12px;
                    font-weight: 400;
                    font-family: "Montserrat", sans-serif;
                    color: #6c6c6c;
                    max-width: 442px;
                    text-align: left;

                    @media (max-width:1024px) {
                        max-width: 300px;
                    }
                }
            }

            .footer-contant-link {

                a {
                    color: #282828;
                    font-size: 14px;
                    font-weight: 500;
                    font-family: "Montserrat", sans-serif;
                    text-align: left;
                    display: block;
                    text-decoration: none;
                    margin: 0 0 26px 0;

                    &:nth-child(5n + 1) {
                        color: #ef404a;
                        font-size: 20px;
                        font-weight: 700;
                        font-family: "Montserrat", sans-serif;
                        text-align: left;
                        margin: 0 0 28px 0;
                        display: block;
                        text-decoration: none;
                    }

                    &:nth-child(5n + 5) {
                        margin: 0;
                    }
                }
            }

            .footer-contant-contact {

                h1 {
                    color: #ef404a;
                    font-size: 20px;
                    font-weight: 700;
                    font-family: "Montserrat", sans-serif;
                    text-align: left;
                    margin: 0 0 28px 0;
                }

                .footer-contact-inner {
                    display: flex;
                    align-items: center;
                    gap: 20px;
                    margin: 0 0 28px 0;

                    @media (max-width:300px) {
                        gap: 10px;
                    }

                    h2 {
                        color: #000;
                        font-size: 14px;
                        font-weight: 500;
                        font-family: "Montserrat", sans-serif;
                        max-width: 325px;
                    }

                    h3 {
                        font-size: 16px;
                        font-weight: 500;
                        color: #000;
                        font-family: "Montserrat", sans-serif;
                    }

                    h4 {
                        font-size: 16px;
                        font-weight: 500;
                        color: #000;
                        font-family: "Montserrat", sans-serif;
                    }
                }
            }
        }

        .copyright-pera-main {
            display: flex;
