body {
    overflow-x: hidden;
}

.slider-container {
    max-width: 1240px;
    margin: 0 auto;
    padding: 0 20px;
}

.slick-arrow {
    display: none !important;
}

.slick-track {
    display: flex !important;
    align-items: center;
    gap: 60px;
    overflow-x: hidden;
    margin: 0 0 60px 0;
    width: 100%;

    @media (max-width:1024px) {
        gap: 20px;
    }

    @media (max-width:576px) {
        gap: 20px;
    }

    // #slider-box-opacity {
    //     opacity: 0.5;
    // }

    .slider-box-one {
        background-color: #FFFFFF;
        border-radius: 12px;
        padding: 24px;
        width: auto !important;
        min-width: calc(348px - 48px);
        height: calc(297px - 48px);

        @media (max-width:400px) {
            min-width: 300px;
        }

        h2 {
            text-align: center;
            font-size: 60px;
            font-weight: 700;
            font-family: "Montserrat", sans-serif;
            color: #ef404a;
        }

        p {
            max-width: 300px;
            font-size: 12px;
            font-weight: 400;
            font-family: "Poppins", sans-serif;
            text-align: center;
            margin: 0 0 12px 0;
            left: 18px;
        }

        .person-image-main {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 0 5px 0;

            img {
                width: 30px;
                height: 30px;
            }
        }

        h3 {
            text-align: center;
            font-size: 12px;
            font-weight: 600;
            font-family: "Poppins", sans-serif;
            color: #282828;
        }
    }
}

.slick-slide {
    width: auto !important;
}

.slick-dots {
    display: flex;
    justify-content: center;
    align-items: center;

    li {
        width: 50px !important;
        height: 20px !important;
        color: transparent !important;

        button {
            width: 50px !important;
            height: 20px !important;
            color: transparent !important;
            background-color: #FFFFFF !important;

            &:before {
                width: 50px !important;
                height: 20px !important;
                color: transparent !important;
                top: 10px !important;
            }
        }

        &.slick-active {
            button {
                width: 10px !important;
                height: 10px !important;
                background-color: #FFF !important;
                border: none !important;
                border-radius: 50% !important;
                cursor: pointer !important;
                content: "" !important;
                top: 10px !important;

                &:before {
                    top: 10px !important;
                    width: 30px !important;
                    height: 10px !important;
                    background-color: #ef404a !important;
                    border: none !important;
                    border-radius: 10px !important;
                    cursor: pointer !important;
                    content: "" !important;
                }
            }
        }
    }
}